// –∏–º–ø–æ—Ä—Ç–∏—Ä—É–µ–º —Å–ø–∏—Å–æ–∫ –ø—Ä–æ–¥—É–∫—Ç–æ–≤ –∏–∑ –º–æ–¥—É–ª—è
import products from "./data_module";
// –∏–º–ø–æ—Ä—Ç–∏—Ä—É–µ—Ç –ø–æ–ª–µ–∑–Ω—ã–µ —Ñ—É–Ω–∫—Ü–∏–∏
import {addHtml, clearHtml} from "./utils";

let totalPrice = 0;
const cart = {};

for (let product of products) {
    cart[product.name] = {...product, count: 0};
}
// –ø–æ—Å–ª–µ —Ü–∏–∫–ª–∞ cart –±—É–¥–µ—Ç —Å–æ–¥–µ—Ä–∂–∞—Ç—å:
// {
//   "–ö–æ—Ç–ª–µ—Ç–∞": {
//     name: "–ö–æ—Ç–ª–µ—Ç–∞",
//     price: 1290,
//     count: 0
//   },
//   ...
// }
// –ø—Ä–∏ —á–µ–º –æ—Ä–∏–≥–∏–Ω–∞–ª—å–Ω—ã–π –æ–±—ä–µ–∫—Ç –ø—Ä–æ–¥—É–∫—Ç–∞ –æ—Å—Ç–∞–Ω–µ—Ç—Å—è –ø—Ä–µ–∂–Ω–∏–º

function renderItem({name, price, count}) {
    // –¥–æ–±–∞–≤–ª—è–µ—Ç –Ω–∞ —Å—Ç—Ä–∞–Ω–∏—Ü—É –≤–µ—Ä—Å—Ç–∫—É –¥–ª—è –æ–¥–Ω–æ–≥–æ –ø—Ä–æ–¥—É–∫—Ç–∞

    addHtml(`
    <p>
      ${name} [ ${price} —Ä—É–± ] - ${count} —à—Ç
      <button class="addTocart" data-name="${name}" data-price="${price}">
        –î–æ–±–∞–≤–∏—Ç—å
      </button>
      
      <button class="deleteTocart" data-name="${name}" data-price="${price}">
        –£–¥–∞–ª–∏—Ç—å
      </button>
    </p>
  `);
}

function renderPage() {
    // —Å–æ–∑–¥–∞–µ—Ç –≤–µ—Ä—Å—Ç–∫—É —Å—Ç—Ä–∞–Ω–∏—Ü—ã

    // –æ—á–∏—â–∞–µ–º –≤—Å–µ
    clearHtml();
    for (let product of products) {
        // –¥–æ–±–∞–≤–ª—è–µ–º –ø–æ –æ–¥–Ω–æ–º—É –ø—Ä–æ–¥—É–∫—Ç—ã –∏–∑ —Å–ø–∏—Å–∫–∞
        renderItem(cart[product.name]);
    }
    // –¥–æ–±–∞–≤–ª—è–µ–º –≤ –∫–æ–Ω—Ü–µ –∏—Ç–æ–≥
    addHtml(`–ò—Ç–æ–≥: ${totalPrice} —Ä—É–±`);

    // –≤—ã–±–∏—Ä–∞–µ—Ç –≤—Å–µ –∫–Ω–æ–ø–∫–∏ –ø–æ –∫–ª–∞—Å—Å—É addTocart
    document.querySelectorAll(".addTocart").forEach((el) => {
        // –Ω–∞–≤–µ—à–∏–≤–∞–µ–º –∏–º —Å–æ–±—ã—Ç–∏–µ –Ω–∞ "–∫–ª–∏–∫"
        el.addEventListener("click", (event) => {
            // –≠–¢–ê –§–£–ù–ö–¶–ò–Ø –í–´–ó–´–í–ê–ï–¢–°–Ø –¢–û–õ–¨–ö–û –ü–†–ò –ö–õ–ò–ö–ï

            // –¥–æ—Å—Ç–∞–µ–º –∏–∑ –∞—Ç—Ä–∏–±—É—Ç–æ–≤ –∑–Ω–∞—á–µ–Ω–∏–µ —Å–≤–æ–π—Å—Ç–≤
            let name = event.target.getAttribute("data-name");
            let price = +event.target.getAttribute("data-price");
            // —É–≤–µ–ª–∏—á–∏–≤–∞–µ–º –∏—Ç–æ–≥
            totalPrice += price;
            // —É–≤–µ–ª–∏—á–∏–≤–∞–µ–º –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ –≤ —Ç–µ–ª–µ–∂–∫–µ
            cart[name].count += 1;
            // –ø–µ—Ä–µ—Ä–∏—Å–æ–≤—ã–≤–∞–µ–º —Å—Ç—Ä–∞–Ω–∏—Ü—É
            renderPage();
        });
    });

    // –≤—ã–±–∏—Ä–∞–µ—Ç –≤—Å–µ –∫–Ω–æ–ø–∫–∏ –ø–æ –∫–ª–∞—Å—Å—É deleteTocart
    document.querySelectorAll(".deleteTocart").forEach((el) => {
        // –Ω–∞–≤–µ—à–∏–≤–∞–µ–º –∏–º —Å–æ–±—ã—Ç–∏–µ –Ω–∞ "–∫–ª–∏–∫"
        el.addEventListener("click", (event) => {
            // –≠–¢–ê –§–£–ù–ö–¶–ò–Ø –í–´–ó–´–í–ê–ï–¢–°–Ø –¢–û–õ–¨–ö–û –ü–†–ò –ö–õ–ò–ö–ï

            // –¥–æ—Å—Ç–∞–µ–º –∏–∑ –∞—Ç—Ä–∏–±—É—Ç–æ–≤ –∑–Ω–∞—á–µ–Ω–∏–µ —Å–≤–æ–π—Å—Ç–≤
            let name = event.target.getAttribute("data-name");
            let price = +event.target.getAttribute("data-price");

            if (cart[name].count > 0) {
                // —É–º–µ–Ω—å—à–∞–µ–º –∏—Ç–æ–≥
                totalPrice -= price;
                // —É–º–µ–Ω—å—à–∞–µ–º –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ –≤ —Ç–µ–ª–µ–∂–∫–µ
                cart[name].count -= 1;
                // –ø–µ—Ä–µ—Ä–∏—Å–æ–≤—ã–≤–∞–µ–º —Å—Ç—Ä–∞–Ω–∏—Ü—É
                renderPage();
            } else if (`${name}` === '–ü–æ–º–∏–¥–æ—Ä—ã') {
                alert(`–ü–æ–º–∏–¥–æ—Ä–æ–≤ –≤ –∫–æ—Ä–∑–∏–Ω–µ –Ω–µ—Ç`)
            } else if (`${name}` === '–ö—É—Ä–∏—Ü–∞') {
                alert(`–ö—É—Ä–∏—Ü—ã –≤ –∫–æ—Ä–∑–∏–Ω–µ –Ω–µ—Ç`)
            } else if (`${name}` === '–ö–æ—Ç–ª–µ—Ç–∞') {
                alert(`–ö–æ—Ç–ª–µ—Ç –≤ –∫–æ—Ä–∑–∏–Ω–µ –Ω–µ—Ç`)
            }
        });
    });
}

renderPage();